body {
  
}
input{
  
  
} 


.header{
  font-family: 'Lobster', cursive;
  color: rgba(255, 255, 255, 0.966);
  text-shadow:  black 3px 3px 3px;
  font-size: 4rem;
  letter-spacing: 10px;
}
.del-btn{
  background-color: rgba(255, 28, 28, 0.747); 
  border:none; 
  

}
.check-btn{
  background-color: rgba(121, 250, 0, 0.61); 
  border:none; 
  

}

.del-btn:hover{
  
  background-color: rgb(255, 0, 0); 
  transition: 0.2s;
  
}
.check-btn:hover{
  
  background-color: rgb(121, 250, 0);
  transition: 0.2s;
}

  
li{
  margin-left: 3%;
}
  

.divs{
 font-size: 1.5rem;
  background-color: rgb(252, 252, 252);
  display: flex;
  justify-content: space-between;
  margin: 20px;
  align-items: center;
  font-family: 'Lobster', cursive;
     font-family: 'Roboto Mono', monospace;
     text-shadow: black 0.001px 0.001px 1px;
     
    
  
  
}

ul{
  list-style: none;
  padding-left: 0px;
  flex-direction: column;
  
}
html{
  height: 100%;
  background: rgb(255,209,0);
  background: linear-gradient(135deg, rgba(255,209,0,1) 0%, rgba(36,2,0,1) 100%, rgba(28,9,121,1) 100%);
  

 
}
.inputtext{
  border: none;
  padding: 1%;
  border-radius: 2px;
  border: 1.5px solid rgb(110, 110, 110);
  box-shadow: rgb(0, 0, 0) 1px 1px 5px;
  animation: animate__fadeInLeft;
  animation-duration: 2s;
}
.add-btn{
  border: none;
  background-color: rgb(76, 238, 76);
  padding: 1%;
  border-radius: 2px;
  border: 1.5px solid rgb(110, 110, 110);
  box-shadow: rgb(0, 0, 0) 1px 1px 5px;
  animation: animate__fadeInLeft;
  animation-duration: 2s;
}
.add-btn:hover{
  cursor: pointer;
  
  background-color: rgb(94, 255, 0);
  transition:0.1s;

}
.fa-plus{
  color: rgb(255, 255, 255);}
  .btns{
    padding: 10px;
  }
  .btns:hover{
    cursor: pointer;
  }
  .change{
    font-size: 1.5rem;
    background-color: rgb(252, 252, 252);
    display: flex;
    justify-content: space-between;
    margin: 20px;
    align-items: center;
    background-color: rgb(255, 255, 255);
   text-transform: capitalize;
   margin:2%;
   font-size: 12px;
   box-shadow: rgb(0, 0, 0) 1px 1px 10px;
   font-family: 'Lobster', cursive;
   font-family: 'Roboto Mono', monospace;
   text-shadow: black 0.001px 0.001px 1px;
   background-color: rgb(189, 189, 189);
   text-decoration: line-through;
   transition: 0.5s
  }
  .fa-facebook {
    text-decoration: none;
    color: #b5b5b5;
    border: 0.1px solid white;
    margin-left: 10px;
    border-radius: 5px;
    padding-left: 6px;
	padding-right: 6px;
	padding-top: 3px;
	padding-bottom: 3px ;
}

.fa-instagram {
	text-decoration: none;
    color: #b5b5b5;
    border: 0.1px solid white;
    margin-left: 10px;
    border-radius: 5px;
    padding-left: 5px;
	padding-right: 5px;
	padding-top: 3px;
	padding-bottom: 3px ;
}
.fa-facebook:hover{
	color: rgb(255, 255, 255);
	border-color: white;
	background-color: rgb(0, 70, 128);
}

.fa-instagram:hover{
	color:white;
	border-color:white;
	background: #f09433; 
background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
}

#myname{
	font: 1.5rem;
	margin-left: 10px;
}
footer{
  position:absolute;
  bottom:0;
  width:100%;
  height:5%;   
  background:rgb(59, 59, 59);
  color: rgb(184, 184, 184);
  display: flex;
  flex-direction: row;
  align-items: center;
}
.fa-whatsapp{
	text-decoration: none;
    color: #b5b5b5;
    border: 0.1px solid white;
    margin-left: 10px;
    border-radius: 5px;
    padding-left: 5px;
	padding-right: 5px;
	padding-top: 3px;
	padding-bottom: 3px ;
}

.fa-whatsapp:hover {
	color:white;
	border-color:white;
	background-color: rgb(77, 128, 0);
}
/* Phone CSS */
