    }
    .add-btn{
      border: none;
      background-color: rgb(76, 238, 76);
      padding: 4%;
      border-radius: 2px;
      border: 1.5px solid rgb(110, 110, 110);
      box-shadow: rgb(0, 0, 0) 1px 1px 5px;
    }
    .fa-plus{
      color: rgb(255, 255, 255);
    }
    .divs{
     background-color: rgb(255, 255, 255);
     text-transform: capitalize;
     margin:2%;
     font-size: 16px;
     box-shadow: rgb(0, 0, 0) 1px 1px 10px;
     font-family: 'Lobster', cursive;
     font-family: 'Roboto Mono', monospace;
     text-shadow: black 0.001px 0.001px 1px;
    }
    .btns{
      padding: 10px;
    }
    .change{
      font-size: 1.5rem;
      background-color: rgb(252, 252, 252);
      display: flex;
      justify-content: space-between;
      margin: 20px;
      align-items: center;
      background-color: rgb(255, 255, 255);
     text-transform: capitalize;
     margin:2%;
     font-size: 12px;
     box-shadow: rgb(0, 0, 0) 1px 1px 10px;
     font-family: 'Lobster', cursive;
     font-family: 'Roboto Mono', monospace;
     text-shadow: black 0.001px 0.001px 1px;
     background-color: rgb(189, 189, 189);
